@import 
    "./mixins-placeholder.scss";

/* ======= MODAL ======= */
.modal-overlay{
    @extend %flex-default;

    width: 100%;
    height: 100%;
    background-color: rgba(0,0, 0, 0.7);
    position: fixed;
    top: 0;

    opacity: 0;
    visibility: hidden;

    z-index: 9999;
}
.modal{
    background-color: #F0F2F5;
    padding: 2.4rem;
    position: relative;
    z-index: 1;
}

.modal-overlay.active {
    opacity: 1;
    visibility: visible;
}
/* ======= RESPONSIVE ======= */

@media (min-width:800px) {
    html {
        font-size: 87.5%;
    }
    #balance {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem
    }
}